import {
  Box,
  Paper,
  TextField as MuiTextField,
  TextFieldProps,
  Typography,
  Button,
  useMediaQuery,
  FormControl,
  InputLabel,
  ListSubheader,
  Select,
  MenuItem,
  Link
} from '@mui/material'
import { Helmet } from 'react-helmet-async'

import { styled, useTheme } from '@mui/material/styles'
import axios, { AxiosResponse } from 'axios'
import { useNavigate } from 'react-router'
import { useState } from 'react'
import departments from 'src/data/departments'
import { useSnackbar } from 'notistack'

const PageWrapper = styled(Box)(
  () => `
      display: flex;
      justify-content: center;
      align-items: center;
  `
)

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

interface RegisterAPIResponse extends AxiosResponse {
  id: number
  name: string
  studentId: string
  phoneNumber: string
  email: string
  department: string
  nickname: string
}

function Register() {
  const navigate = useNavigate()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const [values, setValues] = useState({
    email: null,
    password: null,
    name: null,
    nickname: null,
    studentId: null,
    phoneNumber: null,
    department: null
  })

  const handleChange = event => {
    if (event.target.type === 'password') {
      const password = document.getElementById('password') as HTMLInputElement
      const confirmPassword = document.getElementById('confirm-password') as HTMLInputElement
      if (password.value !== confirmPassword.value) confirmPassword.setCustomValidity("Passwords don't match")
      else confirmPassword.setCustomValidity('')
    }
    if (event.target.id !== 'confirm-password') {
      const newValues = {
        ...values,
        [event.target.name]: event.target.value
      }
      setValues(newValues)
    }
  }
  const handleSubmit = e => {
    e.preventDefault()
    axios
      .post<RegisterAPIResponse>(`${process.env.REACT_APP_APIHOST}/auth/register`, values)
      .then(() => {
        enqueueSnackbar('KLUB의 일원이 되신 것을 환영해요! 이메일 확인 후 로그인해주세요 :)', { variant: 'success' })
        navigate('/auth/login')
      })
      .catch(err =>
        enqueueSnackbar(err.response.statusText ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  return (
    <PageWrapper>
      <Helmet>
        <title>회원가입</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(8),
          padding: 3,
          flex: 1,
          maxWidth: '480px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          KLUB에 회원가입
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField
            id="email"
            name="email"
            type="email"
            label="이메일"
            helperText="학교 이메일을 입력하세요."
            onChange={handleChange}
          />
          <TextField id="password" name="password" type="password" label="비밀번호" onChange={handleChange} />
          <TextField id="confirm-password" type="password" label="비밀번호 확인" onChange={handleChange} />
          <TextField id="name" name="name" type="text" label="이름" onChange={handleChange} />
          <TextField id="nickname" name="nickname" type="text" label="닉네임" onChange={handleChange} />
          <TextField
            id="student-id"
            name="studentId"
            type="text"
            label="학번"
            helperText="학교에서 부여받은 학번을 입력하세요."
            inputProps={{ pattern: '[0-9]{10}' }}
            onChange={handleChange}
          />
          <TextField
            id="phone-number"
            name="phoneNumber"
            type="tel"
            label="전화번호"
            helperText="슬래시(-) 기호를 포함하여 입력하세요. (예) 010-0000-0000"
            inputProps={{ pattern: '[0-9]{3}-[0-9]{4}-[0-9]{4}' }}
            onChange={handleChange}
          />
          <FormControl sx={{ margin: 1, width: '100%' }} size="small" required>
            <InputLabel htmlFor="department">학과/학부</InputLabel>
            <Select
              defaultValue=""
              id="department"
              name="department"
              label="학과/학부"
              onChange={handleChange}
              fullWidth
            >
              {departments &&
                Object.keys(departments).map(college => [
                  <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
                  departments[college] &&
                    departments[college].map(depart => [
                      <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                    ])
                ])}
            </Select>
          </FormControl>
          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            회원가입
          </Button>
          <Link href="/auth/login" sx={{ mt: 1 }}>
            이미 계정이 있으신가요?
          </Link>
        </Box>
      </Paper>
    </PageWrapper>
  )
}

export default Register
